  methods: {
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "页小计";
          }
          if (["goods_num", "subtotal_price"].includes(column.property)) {
            var num = this.$utils.sum(data, column.property);
            if (column.property === "goods_price") {
              return num
                .toFixed(2)
                .toString()
                .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
            }
            return Number.isInteger(num) ? num : num.toFixed(2);
          }
          return null;
        })
        ,
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "合计";
          }
          if (column.property === 'goods_num' && this.footerData.total_goods_num) {
            return this.footerData.total_goods_num
          }
          if (column.property === 'subtotal_price' && this.footerData.total_total_price) {
            return this.footerData.total_total_price
          }
          return null;
        })]
    },
    getTableData(obj) {
      if (obj.data.code === 200) {
        this.footerData = obj.data.data.desc
      }
    }
  }
